<template>
  <div class="universal-input">
    <label for="labelName">{{labelName}}</label>
    <input :type="type" v-model="content" @input="handleInput">
  </div>
</template>

<script>
export default {
  name: 'universal-input',
  props: {
    labelName:String,
    type:String,
  },
  data:function () {
    return {
      content:this.value,
    }
  },
  methods: {
    handleInput (e) {
      this.$emit('input', this.content)
    }
  }
}
</script>

<style>
.universal-input {
  overflow: hidden;
  margin:20px 0;
}
.universal-input > label {
  color:#e1c79b;
  font-size: 14px;
  font-weight: bold;
  display: block;
}
.universal-input > input {
  font-size: 16px;
  width: 100%;
  padding:12px 0;
  color: #f9f9f9;
  background: transparent;
  border:none;
  border-bottom: 1px solid #666464;
  font-family: 'Roboto', sans-serif;
  transition: all .5s;
}
.universal-input > input:focus {
  border-bottom: 1px solid #e1c79b;
}
</style>
