<template>
  <div>
  <form class="register-form">
    <universal-input type="text" v-model="name" labelName="Name"></universal-input>
    <universal-input type="email" v-model="email" labelName="Email"></universal-input>
    <universal-input type="password" v-model="password" labelName="Password"></universal-input>
    <action-button name="Register" :action="register"></action-button>
  </form>
  </div>
</template>

<script>
import UniversalInput from '../components/htmlBlocks/inputs/UniversalInput.vue'
import ActionButton from '../components/htmlBlocks/buttons/ActionButton.vue'
import axios from 'axios'

export default {
  name: 'login-form',
  components: {
    UniversalInput,
    ActionButton,
  },
  data: function () {
    return {
      name: 'ggg',
      password: 'gggg',
      email: '',
      errors: '',
    }
  },
  methods: {
    register: function () {
      let name = this.name
      let password = this.password
      let email = this.email
      this.$store.dispatch('register', { name, email, password })
      .then(resp => alert(resp.data.message + "please login"))
      .catch(err => console.log(err))
      .then(() => this.$router.push('/'))
    },
  }

}
</script>

<style>
.register-form {
  overflow: hidden;
  padding:0 15%;
  margin:5% 0;
}
</style>
